  width: 104px;
  height: 104px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F9F9F9;
}
.grade-card__descr {
  color: #454B58;
  text-align: center;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 17px;
  width: 110px;
}
.grade-card__counter {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: -5px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F04973;
  color: #fff;
  font-size: 13px;
  font-weight: 700;
  line-height: 25px;
}

.teacher-stat-grade {
  padding: 47px 0 0 0;
}
.teacher-stat-grade__title {
  color: #292C32;
  font-family: Montserrat;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 19px;
}
.teacher-stat-grade__icons {
  padding: 21px 0 0 3px;
  display: flex;
  flex-wrap: wrap;
  gap: 31px;
